@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.skills-icons {
    display: flex;
    /* grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr; */
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    width: 100%;
    padding-bottom: 50px;
    position: absolute;
    bottom: 5vh;
    margin: auto;
}

.skills-footer {
    width: 100%;
    height: 30vh;
    background: linear-gradient(180deg, rgba(34, 30, 30, 0.911), rgb(0, 0, 0));
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.skills-footer h1 {
    margin-top: 10vh;
    color: rgb(255, 255, 255);
    letter-spacing: 1.2rem;
    font-family: "Roboto";
    background: #12c2e9; /* fallback for old browsers */
    background: -webkit-linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@media (max-width: 500px) {
    .skills-icons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        justify-content: space-around;
        align-items: center;
    }
    .skills-footer {
        height: 15vh;
    }
    .skills-footer h1 {
        margin: 0;
        font-size: 1rem;
        letter-spacing: 1rem;
    }
}
