* {
    box-sizing: border-box;
}
.card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40vh;
    font-family: Arial, Helvetica, sans-serif;
    margin: 10px 50px;
}

.card {
    position: relative;
    width: 0px;
    height: 0px;
    min-height: 0px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-radius: 1000px;
    transition: 0.5s;
    /* background: linear-gradient(to right, #2193b0, #6dd5ed); */

    background: #12c2e9; /* fallback for old browsers */
    background: -webkit-linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9c9
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.card img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 200px;
    min-height: 180px;
    transition: 0.5s;
    margin: 0 5vh;
    padding: 0 5vh;
    z-index: 1;
}

.card:hover img {
    left: 80%;
    height: 400px;
    min-height: 400px;
}
.card:hover {
    width: 600px;
    height: 200px;
    transition: 0.5s;
    border-radius: 25px 500px 500px 25px;
    margin: 0 10px;
}

.card .content {
    position: relative;
    width: 50%;
    min-width: 50%;
    min-height: 0px;
    left: 20%;
    padding: 40px 20px;
    opacity: 0;
    visibility: hidden;
}
.card:hover .content {
    left: 0%;
    opacity: 1;
    visibility: visible;
}

.card .content h2 {
    color: #fff;
    text-transform: uppercase;
    font-size: 2.5em;
    line-height: 1em;
}

.card .content p {
    color: #fff;
}

.card .content a {
    position: relative;
    display: inline-block;
    color: #111;
    padding: 10px 20px;
    border-radius: 10px;
    background: #fff;
    margin-top: 10px;
    text-decoration: none;
}

@media (max-width: 891px) {
    .card {
        min-height: 0px;
        border-radius: 1000px;
        position: relative;
        width: 0;
        height: 0;
        max-width: 250px;
        transition: 0.5s;
        align-items: flex-start;

        background: #12c2e9; /* fallback for old browsers */
        background: -webkit-linear-gradient(
            -25deg,
            #f64f59,
            #c471ed,
            #12c2e9
        ); /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(
            -25deg,
            #f64f59,
            #c471ed,
            #12c2e9
        ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    }

    .card:hover {
        height: 500px;
        width: 330px;
        transition: 0.5s;
        border-radius: 25px 25px 500px 500px;
    }

    .card img {
        bottom: -90px;
    }

    .card:hover img {
        left: 38.5%;
        bottom: -14%;
        height: 330px;
    }

    .card .content {
        width: 100%;
        left: 0;
        padding: 40px;
    }
}

@media (max-width: 500px) {
    .card-container {
        display: flex;
        position: relative;
        justify-content: space-around;
        align-items: center;
        min-height: 20vh;
        font-family: Arial, Helvetica, sans-serif;
        margin: 0;
        padding: 5vh;
    }
    .card {
        display: flex;
    }
    .card .content {
        padding: 30px;
    }
    .card:hover {
        height: 200px;
        width: 150px;
        min-height: 100px;
    }
    .card img {
        height: 100px;
        min-height: 100px;
        bottom: -50px;
        left: -30px;
    }

    .card:hover img {
        height: 100px;
        min-height: 100px;
        left: 29%;
    }
    .card .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
    }

    .card .content h2 {
        font-size: 1.7rem;
        text-align: center;
    }

    .card .content p {
        margin-top: 5px;
        align-self: flex-start;
    }
}
