@import url(https://fonts.googleapis.com/css?family=Raleway:400,200,300,100,500,700,600,800,900);
:root {
    --dropdown-bg: linear-gradient(to right, #e0eafc, #cfdef3);
}
.clear {
    clear: both;
}

.navigation {
    list-style: none;
    padding: 0;
    color: black;
    margin: 20px;
    font-size: 3rem;
}

.navigation li {
    float: left;
}

.navigation li a {
    display: block;
    text-decoration: none;
    color: #fff;
}

.navigation ul {
    display: none;
    position: absolute;
    list-style: none;
    margin-left: 25px;
    background-color: var(--dropdown-bg);
    overflow: hidden;
    border-radius: 70px;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    border: 1.5px solid #fff;
    border-top: none;
    border-bottom: none;
    box-shadow: 0px 8px 45px -15px #fff;
}

.navigation ul li {
    float: left;
    animation: apear 1s ease;
}

.navigation ul li:nth-child(2) {
    animation: apear 1.7s ease;
}
.navigation ul li:hover {
    filter: drop-shadow(2px 3px 6px);
    transform: translateY(-5px);
    transition: transform 0.5s ease;
}

.navigation li:hover > ul {
    display: block;
}

.navigation {
    position: relative;
}
.navigation li ul {
    left: -25px;
    overflow: hidden;
    padding: 5vh 5px;
}

.on {
    left: -5px;
}

.dropdown-icon {
    width: 117px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes apear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
