:root {
    --text-color: rgb(231, 58, 116);
    --border-color: #3da7f8;
    --animation-time: 3s;
    --text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px var(--text-color), 0 0 10px var(--text-color),
        0 0 10px var(--text-color), 0 0 10px var(--text-color),
        0 0 10px var(--text-color), 0 0 20px var(--text-color),
        0 0 30px var(--text-color), 0 0 40px var(--text-color),
        0 0 55px var(--text-color), 0 0 75px var(--text-color);
}

.aez {
    background-color: transparent;
    font-size: 3rem;
    display: inline-block;
    position: absolute;
    margin-left: 105px;
    padding: 14px 24px;
    transform: rotate(-6deg);
    -webkit-animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(3s * 16) infinite step-end 1s;
            animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(3s * 16) infinite step-end 1s;
    -webkit-animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(var(--animation-time) * 16) infinite step-end 1s;
            animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(var(--animation-time) * 16) infinite step-end 1s;
}

p {
    font-family: Roboto;
    margin: 0px;
    color: #fff;
}

span {
    font-size: 50px;
}

p span {
    opacity: 0.33;
    -webkit-animation: turnOn forwards step-end;
            animation: turnOn forwards step-end;
}

p span:nth-child(1) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

p span:nth-child(2) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

p span:nth-child(3) {
    -webkit-animation: turnOn 0.27s forwards step-end,
        flicker 3s infinite step-end 2s;
            animation: turnOn 0.27s forwards step-end,
        flicker 3s infinite step-end 2s;
    -webkit-animation: turnOn 0.27s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
            animation: turnOn 0.27s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
}

@-webkit-keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@-webkit-keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}

@keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}

.switch {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    cursor: pointer;
}

.switch > span {
    position: absolute;
    top: 14px;
    pointer-events: none;
    font-family: "Helvetica", Arial, sans-serif;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    width: 50%;
    text-align: center;
}

input.check-toggle-round-flat:checked ~ .off {
    color: #f32552;
}

input.check-toggle-round-flat:checked ~ .on {
    color: #fff;
}

.switch > span.on {
    left: 1px;
    padding-left: -5px;
    color: #f32552;
    top: 10px;
    font-size: 1rem;
}

.switch > span.off {
    right: 4px;
    padding-right: 2px;
    color: #fff;
    top: 11px;
    font-size: 1rem;
}

.check-toggle {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden;
}
.check-toggle + label {
    display: block;
    position: relative;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

input.check-toggle-round-flat + label {
    padding: 2px;
    width: 97px;
    height: 35px;
    background-color: #ffffff;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:before,
input.check-toggle-round-flat + label:after {
    display: block;
    position: absolute;
    content: "";
}

input.check-toggle-round-flat + label:before {
    top: 2px;
    left: 2px;
    bottom: 2px;
    right: 2px;
    background-color: #000000;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:after {
    top: 4px;
    left: 4px;
    bottom: 4px;
    width: 48px;
    background-color: #fff;
    border-radius: 52px;
    transition: margin 0.2s;
}

input.check-toggle-round-flat:checked + label:after {
    margin-left: 41px;
}

:root {
    --dropdown-bg: linear-gradient(to right, #e0eafc, #cfdef3);
}
.clear {
    clear: both;
}

.navigation {
    list-style: none;
    padding: 0;
    color: black;
    margin: 20px;
    font-size: 3rem;
}

.navigation li {
    float: left;
}

.navigation li a {
    display: block;
    text-decoration: none;
    color: #fff;
}

.navigation ul {
    display: none;
    position: absolute;
    list-style: none;
    margin-left: 25px;
    background-color: linear-gradient(to right, #e0eafc, #cfdef3);
    background-color: var(--dropdown-bg);
    overflow: hidden;
    border-radius: 70px;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    border: 1.5px solid #fff;
    border-top: none;
    border-bottom: none;
    box-shadow: 0px 8px 45px -15px #fff;
}

.navigation ul li {
    float: left;
    -webkit-animation: apear 1s ease;
            animation: apear 1s ease;
}

.navigation ul li:nth-child(2) {
    -webkit-animation: apear 1.7s ease;
            animation: apear 1.7s ease;
}
.navigation ul li:hover {
    -webkit-filter: drop-shadow(2px 3px 6px);
            filter: drop-shadow(2px 3px 6px);
    transform: translateY(-5px);
    transition: transform 0.5s ease;
}

.navigation li:hover > ul {
    display: block;
}

.navigation {
    position: relative;
}
.navigation li ul {
    left: -25px;
    overflow: hidden;
    padding: 5vh 5px;
}

.on {
    left: -5px;
}

.dropdown-icon {
    width: 117px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@-webkit-keyframes apear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes apear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

* {
    box-sizing: border-box;
}
.card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40vh;
    font-family: Arial, Helvetica, sans-serif;
    margin: 10px 50px;
}

.card {
    position: relative;
    width: 0px;
    height: 0px;
    min-height: 0px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-radius: 1000px;
    transition: 0.5s;
    /* background: linear-gradient(to right, #2193b0, #6dd5ed); */

    background: #12c2e9; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9c9
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.card img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 200px;
    min-height: 180px;
    transition: 0.5s;
    margin: 0 5vh;
    padding: 0 5vh;
    z-index: 1;
}

.card:hover img {
    left: 80%;
    height: 400px;
    min-height: 400px;
}
.card:hover {
    width: 600px;
    height: 200px;
    transition: 0.5s;
    border-radius: 25px 500px 500px 25px;
    margin: 0 10px;
}

.card .content {
    position: relative;
    width: 50%;
    min-width: 50%;
    min-height: 0px;
    left: 20%;
    padding: 40px 20px;
    opacity: 0;
    visibility: hidden;
}
.card:hover .content {
    left: 0%;
    opacity: 1;
    visibility: visible;
}

.card .content h2 {
    color: #fff;
    text-transform: uppercase;
    font-size: 2.5em;
    line-height: 1em;
}

.card .content p {
    color: #fff;
}

.card .content a {
    position: relative;
    display: inline-block;
    color: #111;
    padding: 10px 20px;
    border-radius: 10px;
    background: #fff;
    margin-top: 10px;
    text-decoration: none;
}

@media (max-width: 891px) {
    .card {
        min-height: 0px;
        border-radius: 1000px;
        position: relative;
        width: 0;
        height: 0;
        max-width: 250px;
        transition: 0.5s;
        align-items: flex-start;

        background: #12c2e9; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(
            -25deg,
            #f64f59,
            #c471ed,
            #12c2e9
        ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    }

    .card:hover {
        height: 500px;
        width: 330px;
        transition: 0.5s;
        border-radius: 25px 25px 500px 500px;
    }

    .card img {
        bottom: -90px;
    }

    .card:hover img {
        left: 38.5%;
        bottom: -14%;
        height: 330px;
    }

    .card .content {
        width: 100%;
        left: 0;
        padding: 40px;
    }
}

@media (max-width: 500px) {
    .card-container {
        display: flex;
        position: relative;
        justify-content: space-around;
        align-items: center;
        min-height: 20vh;
        font-family: Arial, Helvetica, sans-serif;
        margin: 0;
        padding: 5vh;
    }
    .card {
        display: flex;
    }
    .card .content {
        padding: 30px;
    }
    .card:hover {
        height: 200px;
        width: 150px;
        min-height: 100px;
    }
    .card img {
        height: 100px;
        min-height: 100px;
        bottom: -50px;
        left: -30px;
    }

    .card:hover img {
        height: 100px;
        min-height: 100px;
        left: 29%;
    }
    .card .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
    }

    .card .content h2 {
        font-size: 1.7rem;
        text-align: center;
    }

    .card .content p {
        margin-top: 5px;
        align-self: flex-start;
    }
}

.skills-icons {
    display: flex;
    /* grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr; */
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    width: 100%;
    padding-bottom: 50px;
    position: absolute;
    bottom: 5vh;
    margin: auto;
}

.skills-footer {
    width: 100%;
    height: 30vh;
    background: linear-gradient(180deg, rgba(34, 30, 30, 0.911), rgb(0, 0, 0));
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.skills-footer h1 {
    margin-top: 10vh;
    color: rgb(255, 255, 255);
    letter-spacing: 1.2rem;
    font-family: "Roboto";
    background: #12c2e9; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #f64f59,
        #c471ed,
        #12c2e9
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@media (max-width: 500px) {
    .skills-icons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        justify-content: space-around;
        align-items: center;
    }
    .skills-footer {
        height: 15vh;
    }
    .skills-footer h1 {
        margin: 0;
        font-size: 1rem;
        letter-spacing: 1rem;
    }
}

:root {
    --dark-ocean-bg: linear-gradient(135deg, #4286f4, #373b44);
    --deep-space-bg: linear-gradient(to right, #000000, #434343);
    --decent-bg: linear-gradient(to right, #4ca1af, #c4e0e5);
    --color-of-sky-bg: linear-gradient(145deg, #e0eafc, #cfdef3);
    --midnight-city: linear-gradient(to top, #232526, #414345);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
    overflow: hidden;
}
body {
    -webkit-animation: fade 2s;
            animation: fade 2s;
    transition: -webkit-animation 2s linear ease;
    transition: animation 2s linear ease;
    transition: animation 2s linear ease, -webkit-animation 2s linear ease;
    background: linear-gradient(145deg, #e0eafc, #cfdef3);
    background: var(--color-of-sky-bg);
    overflow: hidden;
    transition: 0.5s;
}

.body-dark {
    -webkit-animation: fade 2s;
            animation: fade 2s;
    transition: -webkit-animation 2s linear ease;
    transition: animation 2s linear ease;
    transition: animation 2s linear ease, -webkit-animation 2s linear ease;
    background: linear-gradient(135deg, #4286f4, #373b44);
    background: var(--dark-ocean-bg);
    overflow: hidden;
    transition: 0.5s;
}

@-webkit-keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

