* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: auto;
}

.navbar-logo img {
    max-width: 90px;
    transform: rotate(-7deg);
}

.nav-links,
.nav-links-dark {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-link:hover {
   
    background: linear-gradient(
        180deg,
        #f70940,
        #e1345cb4,
        rgba(133, 130, 130, 0.685)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.nav-links ul li:hover ,.nav-links-dark ul li:hover{
    border-bottom: 3px groove rgba(131, 131, 131, 0.74);
}

.nav-links ul,
.nav-links-dark ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: "Redressed", "Lateef", cursive, "Serif";
}

.nav-links ul li,
.nav-links-dark ul li {
    padding: 25px;
    height: 100%;
}

.navbar-logo {
    margin-left: 3%;
    display: flex;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 102px;
}

.navbar-logo a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.nav-links {
    cursor: pointer;
    margin-right: 3%;
    text-decoration: none;
    background: linear-gradient(
        45deg,
        rgba(46, 41, 41, 0.822),
        rgb(29, 29, 29)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.8125rem;
    text-transform: capitalize;
}

.nav-links-dark {
    cursor: pointer;
    margin-right: 3%;
    text-decoration: none;
    background: linear-gradient(
        45deg,
        rgba(255, 255, 255, 0.884),
        rgb(172, 169, 169)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.8125rem;
    text-transform: capitalize;
}

.nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
